'use client';
import { useState, useEffect } from 'react';
import Link from 'next/link';
import React from 'react';
import { getPosts, deletePost } from '../post/service/api';
import PostTable from '../components/PostTable';



export default function PostsPage() {
  const [posts, setPosts] = useState<any[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchPosts = async () => {
    try {
      setIsLoading(true);
      const response = await getPosts();
      setPosts(response.data);
    } catch (err) {
      setError('Erreur lors du chargement des posts');
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  };

  const handleDelete = async (id: string) => {
    try {
      await deletePost(id);
      setPosts(posts.filter(post => post._id !== id));
    } catch (err) {
      setError('Erreur lors de la suppression du post');
      console.error(err);
    }
  };

  useEffect(() => {
    fetchPosts();
  }, []);

  if (isLoading) return <div className="text-center py-8">Chargement...</div>;
  if (error) return <div className="text-center py-8 text-red-500">{error}</div>;

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">Liste des Posts</h1>
        <Link href="/posts/create">
          <button className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded">
            Créer un nouveau post
          </button>
        </Link>
      </div>

      {posts.length === 0 ? (
        <div className="text-center py-8 text-gray-500">Aucun post disponible</div>
      ) : (
        <PostTable posts={posts} onDelete={handleDelete} />
      )}
    </div>
  );
}






